export default {
    data(){
        return{
            isDeleting:false,
        items:[
            {text:"第一组"},
{text:"第二组"},

{text:"第三组"},
]
}},
    methods:{
        showDeleteButton(it) {
            clearInterval(this.Loop);//再次清空定时器，防止重复注册定时器

            var This = this;
            this.Loop = setTimeout(function(){
               alert(it)

            },1000);
        },
        clearLoop() {
            clearInterval(this.Loop);
        },
    }
};



